    useEffect(() => {
        window.scrollTo(0, 0);
        const fetchBlog = async () => {
            try {
                const response = await fetch(`https://api.monacoexperiences.com/api/v1/articles/${blog_id}/`);
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }
                const data = await response.json();
                setBlogDetail(data?.blog);
            } catch (error) {
                console.error('Error fetching blog:', error);
                // Handle error state or logging here
            }
        };
        fetchBlog();
    }, [blog_id]);


    useEffect(() => {
        const fetchBlogs = async () => {
            try {
                const response = await fetch("https://api.monacoexperiences.com/api/v1/articles/category/blog/?limit=10");
                if (!response.ok) {
                    throw new Error("Failed to fetch blogs");
                }
                const data = await response.json();
                setBlogs(data.results);
                setLoading(false);
            } catch (error) {
                setError(error.message);
                setLoading(false);
            }
        };

        fetchBlogs();
    }, []);

    const OpenBlog = (item) => {
        history.push(`${localRoute.blogDetail}/${item.blog_slug}`);
    };

